el);\n\n//   @return mix($color-base, $color, $level * $theme-color-interval);\n// }\n\n// scss-docs-start color-level\n@function theme-color-level($color, $level: 0) {\n  $color-base: if($level > 0, $black, $white);\n  $level: abs($level);\n\n  @return mix($color-base, $color, $level * $theme-color-interval);\n}\n// scss-docs-end color-level\n\n// Return valid calc\n@function add($value1, $value2, $return-calc: true) {\n  @if $value1 == null {\n    @return $value2;\n  }\n\n  @if $value2 == null {\n    @return $value1;\n  }\n\n  @if type-of($value1) == number and type-of($value2) == number and comparable($value1, $value2) {\n    @return $value1 + $value2;\n  }\n\n  @return if($return-calc == true, calc(#{$value1} + #{$value2}), $value1 + unquote(\" + \") + $value2);\n}\n\n@function subtract($value1, $value2, $return-calc: true) {\n  @if $value1 == null and $value2 == null {\n    @return null;\n  }\n\n  @if $value1 == null {\n    @return -$value2;\n  }\n\n  @if $value2 == null {\n    @return $value1;\n  }\n\n  @if type-of($value1) == number and type-of($value2) == number and comparable($value1, $value2) {\n    @return $value1 - $value2;\n  }\n\n  @return if($return-calc == true, calc(#{$value1} - #{$value2}), $value1 + unquote(\" - \") + $value2);\n}\n","@import './_vars.scss';\n@import './animations/fade.scss';\n@import './../../variables';\n@import './popover.scss';\n\n.#{$namespace-prefix}-iOS {\n  cursor: pointer !important;\n  -webkit-tap-highlight-color: transparent;\n}\n\n[data-#{$namespace-prefix}-root] {\n  max-width: calc(100vw - 10px);\n}\n\n.#{$namespace-prefix}-box {\n  position: relative;\n  z-index: $zindex-tooltip;\n  display: block;\n  margin: $tooltip-margin;\n  // background-color: $color;\n  // color: white;\n  // border-radius: 4px;\n  @include reset-text();\n  @include font-size($tooltip-font-size);\n  word-wrap: break-word;\n  outline: 0;\n  transition-property: transform, visibility, opacity;\n\n  &[data-placement^='top'] > .#{$namespace-prefi