 !default;\n$tooltip-color:          $white !default;\n$tooltip-bg:             $black !default;\n$tooltip-border-radius:  $border-radius !default;\n$tooltip-opacity:        .9 !default;\n$tooltip-padding-y:      .25rem !default;\n$tooltip-padding-x:      .5rem !default;\n$tooltip-margin:         0 !default;\n\n$tooltip-arrow-width:    .8rem !default;\n$tooltip-arrow-height:   .4rem !default;\n$tooltip-arrow-color:    $tooltip-bg !default;\n\n// Form tooltips must come after regular tooltips\n$form-feedback-tooltip-padding-y:      $tooltip-padding-y !default;\n$form-feedback-tooltip-padding-x:      $tooltip-padding-x !default;\n$form-feedback-tooltip-font-size:      $tooltip-font-size !default;\n$form-feedback-tooltip-line-height:    $line-height-base !default;\n$form-feedback-tooltip-opacity:        $tooltip-opacity !default;\n$form-feedback-tooltip-border-radius:  $tooltip-border-radius !default;\n","//\n// Base styles\n//\n\n.alert {\n  position: relative;\n  padding: $alert-padding-y $alert-padding-x;\n  margin-bottom: $alert-margin-bottom;\n  border: $alert-border-width solid transparent;\n  @include border-radius($alert-border-radius);\n}\n\n// Headings for larger alerts\n.alert-heading {\n  // Specified to prevent conflicts of changing $headings-color\n  color: inherit;\n}\n\n// Provide class for links that match alerts\n.alert-link {\n  font-weight: $alert-link-font-weight;\n}\n\n\n// Dismissible alerts\n//\n// Expand the right padding and account for the close button's positioning.\n\n.alert-dismissible {\n  @include ltr {\n    padding-right: $close-font-size + $alert-padding-x * 2;\n  }\n  @include rtl {\n    padding-left: $close-font-size + $alert-padding-x * 2;\n  }\n\n  // Adjust close link position\n  .close {\n    position: absolute;\n    top: 0;\n    @include ltr {\n      right: 0;\n    }\n    @include rtl {\n      left: 0;\n    }\n    padding: $alert-padding-y $alert-padding-x;\n    color: inherit;\n  }\n}\n\n\n// Alternate styles\n// scss-docs-start alert-modifiers\n// Generate contextual modifier classes for colorizing the alert.\n\n@include theme-variant() {\n  .alert-#{$color} {\n    @include alert-variant( theme-color-level($value, $alert-bg-level),  theme-color-level($value, $alert-border-level),  theme-color-level($value, $alert-color-level));\n  }\n}\n// scss-docs-end alert-modifiers\n","\n\n// Alerts\n//\n// Define alert colors, border radius, and padding.\n\n// scss-docs-start alert\n$alert-padding-y:         .75rem !default;\n$alert-padding-x:         1.25rem !default;\n$alert-margin-bottom:     1rem !default;\n$alert-border-radius:     $border-radius !default;\n$alert-link-font-weight:  $font-weight-bold !default;\n$alert-border-width:      $border-width !default;\n\n$alert-bg-level:          -10 !default;\n$alert-border-level:      -9 !default;\n$alert-color-level:       6 !default;\n// scss-docs-end alert\n","@mixin alert-variant($background, $border, $color) {\n  color: $color;\n  @include gradient-bg($background);\n  border-color: $border;\n\n  hr {\n    border-top-color: darken($border, 5%);\n  }\n\n  .alert-link {\n    color: darken($color, 10%);\n  }\n}\n","// Gradients\n\n@mixin gradient-bg($color) {\n  @if $enable-gradients {\n    background: $color linear-gradient(180deg, mix($body-bg, $color, 15%), $color) repeat-x;\n  } @else {\n    background-color: $color;\n  }\n}\n\n// Horizontal gradient, from left to right\n//\n// Creates two color stops, start and end, by specifying a color and position for each color stop.\n@mixin gradient-x($start-color: $gray-700, $end-color: $gray-800, $start-percent: 0%, $end-percent: 100%) {\n  background-image: linear-gradient(to right, $start-color $start-percent, $end-color $end-percent);\n  background-repeat: repeat-x;\n}\n\n// Vertical gradient, from top to bottom\n//\n// Creates two color stops, start and end, by specifying a color and position for each color stop.\n@mixin gradient-y($start-color: $gray-700, $end-color: $gray-800, $start-percent: 0%, $end-percent: 100%) {\n  background-image: linear-gradient(to bottom, $start-color $start-percent, $end-color $end-percent);\n  background-repeat: repeat-x;\n}\n\n@mixin gradient-directional($start-color: $gray-700, $end-color: $gray-800, $deg: 45deg) {\n  background-image: linear-gradient($deg, $start-color, $end-color);\n  background-repeat: repeat-x;\n}\n@mixin gradient-x-three-colors($start-color: $blue, $mid-color: $purple, $color-stop: 50%, $end-color: $red) {\n  background-image: linear-gradient(to right, $start-color, $mid-color $color-stop, $end-color);\n  background-repeat: no-repeat;\n}\n@mixin gradient-y-three-colors($start-color: $blue, $mid-color: $purple, $color-stop: 50%, $end-color: $red) {\n  background-image: linear-gradient($start-color, $mid-color $color-stop, $end-color);\n  background-repeat: no-repeat;\n}\n@mixin gradient-radial($inner-color: $gray-700, $outer-color: $gray-800) {\n  background-image: radial-gradient(circle, $inner-color, $outer-color);\n  background-repeat: no-repeat;\n}\n@mixin gradient-striped($color: rgba($white, .15), $angle: 45deg) {\n  background-image: linear-gradient($angle, $color 25%, transparent 25%, transparent 50%, $color 50%, $color 75%, transparent 75%, transparent);\n}\n",".c-avatar {\n  position: relative;\n  display: inline-flex;\n  align-items: center;\n  justify-content: center;\n  border-radius: 50em;\n  @include avatar($avatar-width,$avatar-status-width);\n}\n\n.c-avatar-img {\n  width: 100%;\n  height: auto;\n  border-radius: 50em;\n}\n\n.c-avatar-status {\n  position: absolute;\n  bottom: 0;\n  display: block;\n  border: 1px solid $white;\n  border-radius: 50em;\n\n  @include ltr {\n    right: 0;\n  }\n  @include rtl {\n    left: 0;\n  }\n}\n\n.c-avatar-sm {\n  @include avatar($avatar-sm-width,$avatar-sm-status-width);\n}\n\n.c-avatar-lg {\n  @include avatar($avatar-lg-width,$avatar-lg-status-width);\n}\n\n.c-avatar-xl {\n  @include avatar($avatar-xl-width,$avatar-xl-status-width);\n}\n\n.c-avatars-stack {\n  display: flex;\n\n  .c-avatar {\n    margin-right: - ($avatar-width / 2);\n    transition: margin-right $layout-transition-speed;\n\n    &:hover {\n      margin-right: 0;\n    }\n  }\n\n  .c-avatar-sm {\n    margin-right: - ($avatar-sm-width / 2);\n  }\n\n  .c-avatar-lg {\n    margin-right: - ($avatar-lg-width / 2);\n  }\n\n  .c-avatar-xl {\n    margin-right: - ($avatar-xl-width / 2);\n  }\n}\n\n.c-avatar-rounded {\n  border-radius: $border-radius;\n}\n\n.c-avatar-square {\n  border-radius: 0;\n}\n","@mixin avatar($width, $status-width) {\n  width: $width;\n  height: $width;\n  font-size: $width / 2.5;\n\n  .c-avatar-status {\n    width: $status-width;\n    height: $status-width;\n  }\n}\n","// Avatars\n// scss-docs-start avatars\n$avatar-sm-width:         24px !default;\n$avatar-sm-status-width:  8px !default;\n\n$avatar-width:            36px !default;\n$avatar-status-width:     10px !default;\n\n$avatar-lg-width:         48px !default;\n$avatar-lg-status-width:  12px !default;\n\n$avatar-xl-width:         64px !default;\n$avatar-xl-status-width:  14px !default;\n// scss-docs-end avatars\n","\n// Options\n//\n// Quickly modify global styling by enabling or disabling optional features.\n\n$theme-suffix:                               -theme !default;\n$mobile-breakpoint:                          lg !default;\n$enable-ltr:                                 true !default;\n$enable-rtl:                                 true !default;\n$enable-caret:                               true !default;\n$enable-rounded:                             true !default;\n$enable-shadows:                             false !default;\n$enable-gradients:                           false !default;\n$enable-transitions:                         true !default;\n$enable-prefers-reduced-motion-media-query:  true !default;\n$enable-hover-media-query:                   false !default; // Deprecated, no longer affects any compiled CSS\n$enable-grid-classes:                        true !default;\n$enable-pointer-cursor-for-buttons:          true !default;\n$enable-print-styles:                        true !default;\n$enable-responsive-font-sizes:               false !default;\n$enable-validation-icons:                    true !default;\n$enable-deprecation-messages:                false !default;\n$enable-sidebar-nav-rounded:                 false !default;\n$layout-transition-speed:                    .3s !default;\n$enable-elevations:                          true !default;\n$enable-prefers-color-scheme:                false !default;\n\n$options: () !default;\n\n$default-themes: () !default;\n// stylelint-disable-next-line scss/dollar-variable-default\n$default-themes: map-merge(\n  (\n    default: (\n      \"default\":       true,\n      \"theme-colors\":  $theme-colors,\n      \"grays\":         $grays,\n      \"gradients\":     $theme-gradients\n    )\n  ),\n  $default-themes\n);\n","// Base class\n//\n// Requires one of the contextual, color modifier classes for `color` and\n// `background-color`.\n\n.badge {\n  display: inline-block;\n  padding: $badge-padding-y $badge-padding-x;\n  @include font-size($badge-font-size);\n  font-weight: $badge-font-weight;\n  line-height: 1;\n  text-align: center;\n  white-space: nowrap;\n  vertical-align: baseline;\n  @include border-radius($badge-border-radius);\n  @include transition($badge-transition);\n\n  @at-root a#{&} {\n    @include hover-focus() {\n      text-decoration: none;\n    }\n  }\n\n  // Empty badges collapse automatically\n  &:empty {\n    display: none;\n  }\n}\n\n// Quick fix for badges in buttons\n.btn .badge {\n  position: relative;\n  top: -1px;\n}\n\n// Pill badges\n//\n// Make them extra rounded with a modifier to replace v3's badges.\n\n.badge-pill {\n  padding-right: $badge-pill-padding-x;\n  padding-left: $badge-pill-padding-x;\n  @include border-radius($badge-pill-border-radius);\n}\n\n// Colors\n//\n// Contextual variations (linked badges get darker on :hover).\n\n@include theme-variant() {\n  .badge-#{$color} {\n    @include badge-variant($value, $color);\n  }\n}\n","\n// Badges\n\n// scss-docs-start badges\n$badge-font-size:      75% !default;\n$badge-font-weight:    $font-weight-bold !default;\n$badge-padding-y:      .25em !default;\n$badge-padding-x:      .4em !default;\n$badge-border-radius:  $border-radius !default;\n\n$badge-transition:   $btn-transition !default;\n$badge-focus-width:  $input-btn-focus-width !default;\n\n$badge-pill-padding-x:  .6em !default;\n// Use a higher than normal value to ensure completely rounded edges when\n// customizing padding or font-size on labels.\n$badge-pill-border-radius:  10rem !default;\n// scss-docs-end badges\n\n","// stylelint-disable property-blacklist\n@mixin transition($transition...) {\n  @if length($transition) == 0 {\n    $transition: $transition-base;\n  }\n\n  @if length($transition) > 1 {\n    @each $value in $transition {\n      @if $value == null or $value == none {\n        @warn \"The keyword 'none' or 'null' must be used as a single argument.\";\n      }\n    }\n  }\n\n  @if $enable-transitions {\n    @if nth($transition, 1) != null {\n      transition: $transition;\n    }\n\n    @if $enable-prefers-reduced-motion-media-query and nth($transition, 1) != null and nth($transition, 1) != none {\n      @media (prefers-reduced-motion: reduce) {\n        transition: none;\n      }\n    }\n  }\n}\n","@mixin badge-variant($bg, $color) {\n  color: color-yiq($bg);\n  background-color: $bg;\n\n  @at-root #{str-replace(#{&}, \".badge-#{$color}\", \" \")} a.badge-#{$color} {\n    @include hover-focus() {\n      color: color-yiq($bg);\n      background-color: darken($bg, 10%);\n    }\n\n    &:focus,\n    &.focus {\n      outline: 0;\n      box-shadow: 0 0 0 $badge-focus-width rgba($bg, .5);\n    }\n  }\n}\n",".breadcrumb-menu {\n  @include ltr {\n    margin-left: auto;\n  }\n  @include ltr {\n    margin-right: auto;\n  }\n\n\n  &::before {\n    display: none;\n  }\n\n  .btn-group {\n    vertical-align: top;\n  }\n\n  .btn {\n    padding: 0 $input-btn-padding-x;\n    vertical-align: top;\n    border: 0;\n\n    &:hover,\n    &.active {\n      color: $body-color;\n      background: transparent;\n    }\n    @include themes($typography-theme-map, $create: parent) {\n      color: themes-get-value(\"text-muted\");\n    }\n  }\n\n  .show {\n    .btn {\n      color: $body-color;\n      background: transparent;\n    }\n  }\n\n  .dropdown-menu {\n    min-width: 180px;\n    line-height: $line-height-base;\n  }\n}\n","//\n// Base styles\n//\n\n.breadcrumb {\n  display: flex;\n  flex-wrap: wrap;\n  padding: $breadcrumb-padding-y $breadcrumb-padding-x;\n  margin-bottom: $breadcrumb-margin-bottom;\n  @include font-size($breadcrumb-font-size);\n  list-style: none;\n  @include border-radius($breadcrumb-border-radius);\n  @include borders($breadcrumb-borders);\n\n  @include themes($breadcrumb-theme-map, $create: parent) {\n    background-color: themes-get-value(\"breadcrumb-bg\");\n    border-color: themes-get-value(\"breadcrumb-border-color\");\n  }\n}\n\n.breadcrumb-item {\n  display: flex;\n\n  // The separator between breadcrumbs (by default, a forward-slash: \"/\")\n  + .breadcrumb-item {\n    @include ltr {\n      padding-left: $breadcrumb-item-padding;\n    }\n    @include rtl {\n      padding-right: $breadcrumb-item-padding;\n    }\n\n    &::before {\n      display: inline-block; // Suppress underlining of the separator in modern browsers\n      @include themes($breadcrumb-theme-map, $create: parent) {\n        color: themes-get-value(\"breadcrumb-divider-color\");\n      }\n      content: escape-svg($breadcrumb-divider);\n      @include ltr {\n        padding-right: $breadcrumb-item-padding;\n      }\n      @include rtl {\n        padding-left: $breadcrumb-item-padding;\n      }\n    }\n  }\n\n  // IE9-11 hack to properly handle hyperlink underlines for breadcrumbs built\n  // without `<ul>`s. The `::before` pseudo-element generates an element\n  // *within* the .breadcr