arent.\n\n.list-group-item {\n  position: relative;\n  display: block;\n  padding: $list-group-item-padding-y $list-group-item-padding-x;\n  text-decoration: if($link-decoration == none, null, none);\n  border: $list-group-border-width solid;\n  @include themes($list-group-theme-map, $create: parent) {\n    color: themes-get-value(\"list-group-color\");\n    background-color: themes-get-value(\"list-group-bg\");\n    border-color: themes-get-value(\"list-group-border-color\");\n  }\n\n  &:first-child {\n    @include border-top-radius(inherit);\n  }\n\n  &:last-child {\n    @include border-bottom-radius(inherit);\n  }\n\n  &.disabled,\n  &:disabled {\n    pointer-events: none;\n    @include themes($list-group-theme-map, $create: parent) {\n      color: themes-get-value(\"list-group-disabled-color\");\n      background-color: themes-get-value(\"list-group-disabled-bg\");\n    }\n  }\n\n  // Include both here for `<a>`s and `<button>`s\n  &.active {\n    z-index: 2; // Place active items above their siblings for proper border styling\n    @include themes($list-group-theme-map, $create: parent) {\n      color: themes-get-value(\"list-group-active-color\");\n      background-color: themes-get-value(\"list-group-active-bg\");\n      border-color: themes-get-value(\"list-group-active-border-color\");\n    }\n  }\n\n  & + & {\n    border-top-width: 0;\n\n    &.active {\n      margin-top: -$list-group-border-width;\n      border-top-width: $list-group-border-width;\n    }\n  }\n}\n\n\n// Horizontal\n//\n// Change the layout of list group items from vertical (default) to horizontal.\n\n@each $breakpoint in map-keys($grid-breakpoints) {\n  @include media-breakpoint-up($breakpoint) {\n    $infix: breakpoint-infix($breakpoint, $grid-breakpoints);\n\n    .list-group-horizontal#{$infix} {\n      flex-direction: row;\n\n      .list-group-item {\n        &:first-child {\n          @include border-bottom-left-radius($list-group-border-radius);\n          @include border-top-right-radius(0);\n        }\n\n        &:last-child {\n          @include border-top-right-radius($list-group-border-radius);\n          @include border-bottom-left-radius(0);\n        }\n\n        &.active {\n          margin-top: 0;\n        }\n\n        & + .list-group-item {\n          border-top-width: $list-group-border-width;\n          border-left-width: 0;\n\n          &.active {\n            margin-left: -$list-group-border-width;\n            border-left-width: $list-group-border-width;\n          }\n        }\n      }\n    }\n  }\n}\n\n\n// Flush list items\n//\n// Remove borders and border-radius to keep list group items edge-to-edge. Most\n// useful within other components (e.g., cards).\n\n.list-group-flush {\n  @include border-radius(0);\n\n  > .list-group-item {\n    border-width: 0 0 $list-group-border-width;\n\n    &:last-child {\n      border-bottom-width: 0;\n    }\n  }\n}\n\n\n// Contextual variants\n// scss-docs-start list-group-modifiers\n// Add modifier classes to change text and background color on individual items.\n// Organizationally, this must come after the `:hover` states.\n\n@include theme-variant() {\n  @include list-group-item-variant($color,  theme-color-level($value, -9),  theme-color-level($value, 6));\n}\n// scss-docs-end list-group-modifiers\n// List items with accent\n//\n// Remove top, bottome and right borders and border-radius.\n\n.list-group-accent {\n  .list-group-item {\n    margin-bottom: 1px;\n    border-top: 0;\n    border-right: 0;\n    border-bottom: 0;\n    @include border-radius(0);\n\n    &.list-group-item-divider {\n      position: relative;\n\n      &::before {\n        position: absolute;\n        bottom: -1px;\n        width: 90%;\n        height: 1px;\n        content: \"\";\n\n        @include ltr {\n          left: 5%;\n        }\n        @include rtl {\n          right: 5%;\n        }\n        @include themes($list-group-theme-map) {\n          background-color: themes-get-value(\"list-group-border-color\");\n        }\n      }\n    }\n  }\n  // Contextual variants\n  //\n  // Add modifier classes to change border color on individual items.\n  @include theme-variant() {\n    @include list-group-item-accent-variant($color, $value);\n  }\n}\n","// List group\n\n// scss-docs-start list-group\n$list-group-border-width:   $border-width !default;\n$list-group-border-radius:  $border-radius !default;\n\n$list-group-item-padding-y:  .75rem !default;\n$list-group-item-padding-x:  1.25rem !default;\n// scss-docs-end list-group\n\n// Default theme\n// scss-docs-start list-group-default-theme\n$list-group-color:                null !default;\n$list-group-bg:                   inherit !default;\n$list-group-border-color:         rgba($black, .125) !default;\n$list-group-hover-bg:             $gray-100 !default;\n$list-group-active-color:         $component-active-color !default;\n$list-group-active-bg:            $component-active-bg !default;\n$list-group-active-border-color:  $list-group-active-bg !default;\n$list-group-disabled-color:       $gray-600 !default;\n$list-group-disabled-bg:          $list-group-bg !default;\n$list-group-action-color:         $gray-700 !default;\n$list-group-action-hover-color:   $list-group-action-color !default;\n$list-group-action-active-color:  $body-color !default;\n$list-group-action-active-bg:     $gray-200 !default;\n// scss-docs-end list-group-default-theme\n\n$list-group-theme-map: () !default;\n// stylelint-disable-next-line scss/dollar-variable-default\n$list-group-theme-map: map-merge(\n  (\n    default: (\n      \"list-group-color\":                $list-group-color,\n      \"list-group-bg\":                   $list-group-bg,\n      \"list-group-border-color\":         $list-group-border-color,\n      \"list-group-hover-bg\":             $list-group-hover-bg,\n      \"list-group-active-color\":         $list-group-active-color,\n      \"list-group-active-bg\":            $list-group-active-bg,\n      \"list-group-active-border-color\":  $list