themes($table-theme-map) {\n        color: themes-get-value(\"table-head-color\");\n        background-color: themes-get-value(\"table-head-bg\");\n        border-color: themes-get-value(\"table-border-color\");\n      }\n    }\n  }\n}\n\n.table-dark {\n  @include themes($table-theme-map) {\n    color: themes-get-value(\"table-dark-color\");\n    background-color: themes-get-value(\"table-dark-bg\");\n  }\n\n  th,\n  td,\n  thead th {\n    @include themes($table-theme-map) {\n      border-color: themes-get-value(\"table-dark-border-color\");\n    }\n  }\n\n  &.table-bordered {\n    border: 0;\n  }\n\n  &.table-striped {\n    tbody tr:nth-of-type(#{$table-striped-order}) {\n      @include themes($table-theme-map) {\n        background-color: themes-get-value(\"table-dark-accent-bg\");\n      }\n    }\n  }\n\n  &.table-hover {\n    tbody tr {\n      @include hover() {\n        @include themes($table-theme-map) {\n          color: themes-get-value(\"table-dark-hover-color\");\n          background-color: themes-get-value(\"table-dark-hover-bg\");\n        }\n      }\n    }\n  }\n}\n\n\n// Responsive tables\n//\n// Generate series of `.table-responsive-*` classes for configuring the screen\n// size of where your table will overflow.\n\n.table-responsive {\n  @each $breakpoint in map-keys($grid-breakpoints) {\n    $next: breakpoint-next($breakpoint, $grid-breakpoints);\n    $infix: breakpoint-infix($next, $grid-breakpoints);\n\n    &#{$infix} {\n      @include media-breakpoint-down($breakpoint) {\n        display: block;\n        width: 100%;\n        overflow-x: auto;\n        -webkit-overflow-scrolling: touch;\n\n        // Prevent double border on horizontal scroll due to use of `display: block;`\n        > .table-bordered {\n          border: 0;\n        }\n      }\n    }\n  }\n}\n\n.table-outline {\n  border: 1px solid;\n\n  @include themes($table-theme-map, $create: parent) {\n    border-color: themes-get-value(\"table-border-color\");\n  }\n\n  td {\n    vertical-align: middle;\n  }\n}\n\n.table-align-middle {\n\n  td {\n    vertical-align: middle;\n  }\n}\n\n.table-clear {\n  td {\n    border: 0;\n  }\n}\n","// Tables\n\n@mixin table-row-variant($state, $background, $border: null) {\n  // Exact selectors below required to override `.table-striped` and prevent\n  // inheritance to nested tables.\n  .table-#{$state} {\n    &,\n    > th,\n    > td {\n      @if map-has-key($yiq-theme-map, $current-theme) {\n        color: color-yiq($background, map-get(map-get($yiq-theme-map, $current-theme), \"yiq-text-dark\"), map-get(map-get($yiq-theme-map, $current-theme), \"yiq-text-light\"));\n      }\n      background-color: $background;\n    }\n\n    @if $border != null {\n      th,\n      td,\n      thead th,\n      tbody + tbody {\n        border-color: $border;\n      }\n    }\n  }\n\n  // Hover states for `.table-hover`\n  // Note: this is not available for cells or rows within `thead` or `tfoot`.\n  .table-hover {\n    $hover-background: darken($background, 5%);\n\n    .table-#{$state} {\n      @include hover() {\n        background-color: $hover-background;\n\n        > td,\n        > th {\n          background-color: $hover-background;\n        }\n      }\n    }\n  }\n}\n",".toast {\n  width: $toast-max-width;\n  max-width: $toast-max-width;\n  overflow: hidden; // cheap rounded corners on nested items\n  @include font-size($toast-font-size);\n  background-clip: padding-box;\n  border: $toast-border-width solid;\n  box-shadow: $toast-box-shadow;\n  backdrop-filter: blur(10px);\n  opacity: 0;\n  @include border-radius($toast-border-radius);\n  @include themes($toast-theme-map, $create: parent) {\n    color: themes-get-value(\"toast-color\");\n    background-color: themes-get-value(\"toast-background-color\");\n    border-color: themes-get-value(\"toast-border-color\");\n  }\n\n  &:not(:last-child) {\n    margin-bottom: $toast-padding-x;\n  }\n\n  &.showing {\n    opacity: 1;\n  }\n\n  &.show {\n    display: block;\n    opacity: 1;